@import "..//..//../variables.scss";

.btn {
  font-size: 1.3rem;
  color: $btn-text;
  font-family: "Lato";
  font-weight: 800;


  padding: 0.75rem 3.65rem;

  background-color: $main-color;
  border-radius: 65rem;

  cursor: pointer;

  &:hover {
    background-color: $hover-color;
  }
}
